#header {
  background-color: #000;
  padding-top: 10px;
  padding-bottom: 10px
}

.header-content {
  min-height: 0;
  padding: 0
}

.Brand {
  padding-left: 10px
}

.Brand__img {
  width: 45px;
  height: 46px
}

.Brand__img--desktop {
  width: 40%;
  height: auto;
  margin-top: 6px;
  margin-bottom: 6px;
  fill: #fff
}
#header .svg-inline--fa{
  color: #fff !important
}

.menu-toggle {
  right: 82px;
  left: auto !important;
  margin: 0 !important;
  top: 10px !important
}

.btn.shopping-cart-link {
  @media screen and (min-width: 992px) {
    color: #fff !important;
    border-color: #fff !important;
    background-color: transparent !important;
  }
}

.btn.shopping-cart-link {
  color: #fff;
  background-color: #783DF6;
  border: 2px solid #783DF6;
}

.btn.shopping-cart-link:hover {
  background-color: #310F7A;
  border: 2px solid #310F7A;
}

#header .menu-toggle:focus,
#header .menu-toggle:hover,
.btn.shopping-cart-link:focus {
  background-color: #783df6 !important;
  border-color: #783df6 !important;
  text-decoration: none !important
}
#header .menu-toggle {
  border: 2px solid #fff !important;
  width: 46px;
  height: 46px;
  font-size: 25px;
  padding: 0;
  top: 7px !important;
}
#header  .menu-toggle.sidebar-button {
  right: 28px;
}

#header .menu-toggle span.mobile-cart-indicator {
  background-color: #fff;
  font-size: 65%;
  color:black;
}

#header .menu-toggle:focus:focus span.mobile-cart-indicator,
#header .menu-toggle:focus:hover span.mobile-cart-indicator,
#header .menu-toggle:hover:focus span.mobile-cart-indicator,
#header .menu-toggle:hover:hover span.mobile-cart-indicator {
  color: #000 !important
}


.customer-options {
  padding: 4px 0 0;
  height: 70px
}

.order-message{
  font-style: normal;
}



#orderInfoSecondBar .form-group{
  margin-bottom: 0
}

#orderInfoSecondBar {
  padding: 0;
  margin: 0
}

.btn.shopping-cart-link:focus{
  box-shadow: inset 0 0 0 3px #ff22c9 !important;
  outline: 0 !important;
  height: auto
}

@media (max-width:575px) {
  #header {
    height: 66px;
  }
  #header .menu-toggle {
    top: 10px !important;
  }
  .Brand__img--mobile {
    height: 100%;
    width: auto;
    margin-left: 10px;
    margin-top: 6px;
  }
  .Brand__img--desktop {
    width: auto;
    height: auto;
    display: none
  }
  #header .menu-toggle {
    right: 70px;
  }
  #header .menu-toggle.sidebar-button {
    right: 15px;
  }
}

@media (min-width: 576px) {
  .Brand__img--mobile{
    display: none
  }
}

@media (max-width: 991.98px) {
  .Brand__img--desktop {
    width: auto;
    height: auto;
    display: none
  }
}
@media (min-width: 992px) {
  #header {
    padding-top: 74px;
    .menu-toggle {
      display: none;
    }
  }
  #customer-order-info{
    display: grid;
    grid-template-columns: 3fr 5fr auto;
    .order-message{
      font-weight: 600;
      letter-spacing: 0.01em;
      #orderInfoEmptyMessage{
        padding-top: 12px;
        display: inline-block;
      }
      #orderInfoItemCount{
        padding-top: 5px;
        display: inline-block;
      }
    }
    .customer-buttons{
      .cart-callout{
        #orderTimeout{
          position: absolute;
          right: 149px;
          margin-top: 22px;
          color: white;
          font-style: normal;
        }
      }
    }
  }
}